<template>
  <div>
    <main>
      <div class="company">
        <div class="container fl-ex">
          <!-- 左部分 -->
          <div class="left">
            <el-form :label-position="labelPosition" :model="formLabelAlign">
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:60%; margin-bottom:-5%;">单位名称</p>
                <el-input v-model="formLabelAlign.name" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:60%; margin-bottom:-5%;">联系方式</p>
                <el-input v-model="formLabelAlign.region" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:60%; margin-bottom:-5%;">联系人</p>
                <el-input v-model="formLabelAlign.type" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:60%; margin-bottom:-5%;">法人信息</p>
                <el-input v-model="formLabelAlign.know" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
              <div style="margin-top:7%;">
                <p style="margin-right:60%; font-size:0.87rem;">申请机构:</p>
                <el-select v-model="value" placeholder="请选择工会" style="width:93%; height:2.5rem;">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </div>
              <div style="margin-top:6.5%; margin-bottom:-10%;">
                <p style="margin-right:70%; font-size:0.87rem;">申请表</p>
                <div class="fl-ex">
                  <el-form-item>
                    <el-input v-model="formLabelAlign.book" style="width:90%; height:2.5rem;"></el-input>
                  </el-form-item>
                  <el-upload
                    class="upload-demo"
                    action="https://jsonplaceholder.typicode.com/posts/"
                  >
                    <el-button size="small" style="width:90%; height:2.5rem;">上传文件</el-button>
                  </el-upload>
                </div>
              </div>
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:60%; margin-bottom:-5%;">主席名称</p>
                <el-input v-model="formLabelAlign.zhu" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:60%; margin-bottom:-5%;">身份证号码</p>
                <el-input v-model="formLabelAlign.card" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:60%; margin-bottom:-5%;">联系电话</p>
                <el-input v-model="formLabelAlign.card" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
            </el-form>
          </div>
          <!-- 右部分 -->
          <div class="right">
            <el-form :label-position="labelPosition" :model="formLabelAlign">
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:60%; margin-bottom:-5%;">申请状态</p>
                <el-input v-model="formLabelAlign.state" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:60%; margin-bottom:-5%;">联系地址</p>
                <el-input v-model="formLabelAlign.add" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
              <div class="block" style="margin-top:7.7%; ">
                <span class="demonstration" style="margin-right:60%; font-size:0.87rem;">申请时间:</span>
                <el-date-picker
                  v-model="value1"
                  type="date"
                  placeholder="选择日期"
                  style="width:93%; height:2.5rem;"
                ></el-date-picker>
              </div>
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:60%; margin-bottom:-5%; font-size:0.87rem;">组成人数</p>
                <el-input v-model="formLabelAlign.member" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
              <div style="margin-top:7%;">
                <p style="margin-right:60%; font-size:0.87rem;">所在地:</p>
                <el-select v-model="value" placeholder="请选择" style="width:93%; height:2.5rem;">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </div>
              <div style="margin-top:7.3%; margin-bottom:-10%;">
                <p style="margin-right:60%; font-size:0.87rem;">其他资料</p>
                <div class="fl-ex">
                  <el-form-item>
                    <el-input v-model="formLabelAlign.book" style="width:90%; height:2.5rem;"></el-input>
                  </el-form-item>
                  <el-upload
                    class="upload-demo"
                    action="https://jsonplaceholder.typicode.com/posts/"
                  >
                    <el-button size="small" style="width:90%; height:2.5rem;">上传文件</el-button>
                  </el-upload>
                </div>
              </div>
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:60%; margin-bottom:-5%;">政党职务</p>
                <el-input v-model="formLabelAlign.duty" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:60%; margin-bottom:-5%;">所获荣誉</p>
                <el-input v-model="formLabelAlign.honor" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
              <div style="margin-top:6.5%; margin-bottom:-10%;">
                <p style="margin-right:50%; font-size:0.87rem;">个人资料上传</p>
                <div class="fl-ex">
                  <el-form-item>
                    <el-input v-model="formLabelAlign.book" style="width:90%; height:2.5rem;"></el-input>
                  </el-form-item>
                  <el-upload
                    class="upload-demo"
                    action="https://jsonplaceholder.typicode.com/posts/"
                    
                  >
                    <el-button size="small" style="width:90%; height:2.5rem;">上传文件</el-button>
                  </el-upload>
                </div>
              </div>
            </el-form>
          </div>
        </div>
        <!-- 按钮 -->
        <el-row style="margin-top:25px;">
          <el-button type="primary" style="width:80px; height:35px;">保存</el-button>
          <el-button type="success" style="width:80px; height:35px;">提交</el-button>
        </el-row>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 单输入框
      labelPosition: "top",
      formLabelAlign: {
        name: "",
        region: "",
        type: "",
        know: "",
        book: "",
        zhu: "",
        card: "",
        tel: "",
        state: "",
        add: "",
        member: "",
        duty: "",
        honor: ""
      },
      // 下拉选择框
        options: [{
          value: '数据一',
          label: '数据二'
        }],
        value: '',
      value1: "",
      // 选择日期
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
    };  
  },
};
</script>

<style scoped>
main {
  display: flex;
  flex-direction: column;
  overflow: auto;
  flex: 1;
}
main .company {
  width: 100%;
  height: 47rem;
  background-color: #e2e2e2;
}
main .company .container {
  width: 96%;
  height: 42rem;
  margin: 0 auto;
  background-color: white;
  margin-top: 2%;
}
main .company .left {
  width: 50%;
  height: 42rem;
}
main .company .right {
  width: 50%;
  height: 42rem;
}
</style>


